import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/Home'
import Music from '@/Music'
import Notebook from '@/Notebook'
import Photos from '@/Photos'
import Play from '@/components/Play'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          path: '',
          components: {
            play: Play
          }
        }
      ]
    },
    {
      path: '/music',
      name: 'music',
      component: Music
    },
    {
      path: '/notebook',
      name: 'notebook',
      component: Notebook
    },
    {
      path: '/photos',
      name: 'photos',
      component: Photos
    }
  ]
})



