<template>
  <div class="cpt-play">
    <ul>
      <li
        v-for="img in imgs"
        :img = "img"
        :key="img.id">
        <transition name="fade-cude">
          <img :src="img.link" :alt="img.name" v-show="img.isAct">
        </transition>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'play',
  data () {
    return {
      imgs: [
        { link: '../static/img/1.jpg', name: '女一号', isAct: true },
        { link: '../static/img/2.jpg', name: '女二号', isAct: false },
        { link: '../static/img/3.jpg', name: '女三号', isAct: false },
        { link: '../static/img/4.jpg', name: '女四号', isAct: false },
        { link: '../static/img/5.jpg', name: '女五号', isAct: false },
        { link: '../static/img/6.jpg', name: '女六号', isAct: false }
      ],
      index: 0,
      timer: null,
      forward: true
    }
  },
  created () {
    this.change()
  },
  methods: {
    change: function () {
      this.timer = setInterval(this.autoChange, 2000)
    },
    autoChange: function () {
      let len = this.imgs.length
      this.forward
      ? (this.index === len - 1
        ? (this.forward = !this.forward)
        : this.index++)
      : (this.index === 1
        ? (this.forward = !this.forward)
        : this.index--)
      this.imgs[this.index].isAct = !this.imgs[this.index].isAct
    }
  }
}
</script>

<style scoped>
  .cpt-play{
    float: left;
    height: 422px;
    transform-style: preserve-3d;
    /*transform: rotateX(-30deg);*/
  }
  .cpt-play li {
    list-style: none;
    position: absolute;
    overflow: hidden;
    width: 420px;
    height: 245px;
    padding: 9px;
    border: 1px solid #eee;
    box-sizing: border-box
  }
  img{
    height: 225px;
   }
  .fade-cude-enter-active {
    transform-style: preserve-3d;
    animation: fadeCudeIn 1.5s ease-in-out;
  }
  .fade-cude-leave-active {
    transform-style: preserve-3d;
    animation: fadeCudeOut 1.5s ease-in-out;
  }
  @keyframes fadeCudeIn {
    from{ opacity: 0;}
    to{ opacity: 1;}
  }
  @keyframes fadeCudeOut {
    from{ opacity: 1}
    to{ opacity: 0;}
  }
</style>



