<template>
  <div class="music">
    <audio preload="auto" :src="song"></audio>
    <input type="text" v-model="musicName" placeholder="请输入歌名">
    <button @click="search(musicName)">搜索</button>
    <select v-model="mApi">
      <option
       v-for="api in apis"
       :key="api.id">{{api.name}}</option>
    </select>
    <div class="info">
      <p>{{lrc}}</p>
    </div>
    <div class="control">
      <i @click="play()" :class="[!isPlayed ? 'pause-btn' : 'play-btn']">
        <i></i>
        <i></i>
        <i></i>
      </i>
    </div>
    <dl
     v-for="list in source.song_list"
     :key="list.id">
      <dt>{{list.title}}</dt>
      <dd><button @click="getUrl(list)">播放</button></dd>
    </dl>
    
  </div>
</template>

<script>
export default {
  name: 'music',
  data () {
    return {
      musicName: '',
      source: '',
      song: 'http://zhangmenshiting.baidu.com/data2/music/be8d64ada7670a3e6141773d00dfbf02/540999948/540999948.mp3?xcode=40d5f688c14e33e919a583210f1a2e09',
      lrc: '',
      player: '',
      apis: [
        {name: '网易云', link: 'https://api.imjad.cn/cloudmusic/'},
        {name: '百度音乐', link: 'http://tingapi.ting.baidu.com/v1/restserver/ting/?method=baidu.ting.search.common&query='}
      ],
      mApi: '',
      isPlayed: false
    }
  },
  mounted () {
    this.player = document.querySelector('audio')
  },
  methods: {
    search (name) {
      let api = this.apis[1].link
      for (let i in this.apis) {
        if (this.apis[i].name === this.mApi) {
          api = this.apis[i].link
        }
      }
      console.log(api)
      this.$http.jsonp(api + name).then(res => {
        this.source = res.body
      })
    },
    getUrl (list) {
      this.$http.jsonp('http://tingapi.ting.baidu.com/v1/restserver/ting/?method=baidu.ting.song.play&songid=' + list.song_id).then(res => {
        res.body = JSON.stringify(res.body)
        res.body = JSON.parse(res.body)
        this.song = res.body['bitrate']['file_link']
        this.lrc = res.body.lrclink
        console.log(this.lrc)
      })
    },
    play () {
      if (!this.isPlayed) {
        this.player.play()
        this.isPlayed = true
      } else {
        this.player.pause()
        this.isPlayed = false
      }
    }
  },
  watch: {
    song: function () {
      if (this.player.readyState === 4) {
        setTimeout(() => {
          this.player.play()
        }, 500)
      }
      this.isPlayed = true
    }
  }
}
</script>

<style lang="less" scoped>
.transform (@style){
  -webkit-transform: @style;
  transform: @style
}
.info{}
  .control{
    .pause-btn, .play-btn{
      display: inline-block;
      width: 54px;
      height: 54px;
      cursor: pointer;
      i{
        position: absolute;
        display: inline-block;
        width: 50px;
        height: 4px;
        background: #fde;
        transform-origin: left;
        transition: all 1s;
      }
    }
    .pause-btn{
      i{
        position: absolute;
        display: inline-block;
        width: 50px;
        height: 4px;
        background: #fde;
        transform-origin: left;
        transition: all 1s;
        &:nth-child(1) {
          transform: rotate(90deg)
        }
        &:nth-child(2) {
          transform: rotate(30deg)
        }
        &:nth-child(3) {
          transform: translateY(50px) rotate(-30deg)
        }
      }
    }
    .play-btn{
      i{
        background: #f0f;
        &:nth-child(1) {
          background: transparent
        }
        &:nth-child(2) {
          transform: rotate(90deg) translateY(-15px)
        }
        &:nth-child(3) {
          transform: rotate(90deg) translateY(-35px)
        }
      }
    }
  }
</style>



