<template>
  <div id="app">
    <div class="main-nav">
      <img src="./assets/logo.png" height="30">
      <input type="search" placeholder="大家都在搜：...">
      <div class="nav-bar">
        <button>
          <i></i>
          <i></i>
          <i></i>
        </button>
        <ul class="f-cb">
          <li>
            <router-link to="/">首页</router-link>
          </li>
          <li
          v-for="link in links"
          :key="link.id">
            <router-link :to="{name: link.name}">{{ link.title }}</router-link>
          </li>
          <span class="wei"></span>
        </ul>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import './assets/css/reset.css'

export default {
  name: 'app',
  data () {
    return {
      links: [
        { name: 'music', title: '音乐' },
        { name: 'notebook', title: '日记本' },
        { name: 'photos', title: '相册' }
      ]
    }
  }
}
</script>

<style lang='less'>
.f-cb:after,.f-cbli li:after{display:block;clear:both;visibility:hidden;height:0;overflow:hidden;content:".";}
.box-shadow(@style, @c) when (iscolor(@c)) {
  -webkit-box-shadow: @style @c;
  box-shadow:         @style @c;
}
.box-shadow(@style, @alpha: 50%) when (isnumber(@alpha)) {
  .box-shadow(@style, rgba(0, 0, 0, @alpha));
}
#app {
  width: 1280px;
  margin: 50px auto 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  .main-nav {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #ddd;
    img, input{
      margin: 0 15px;
      vertical-align: middle
    }
    input{
      width: 470px;
      height: 30px;
      border: 1px solid #ddd;
      background: #F2F2F5;
      padding: 0 1em
    }
    button {
      display: none;
      float: right;
      width: 50px;
      height: 50px;
      border: 2px solid #F2F2F5;
      border-radius: 5px;
      background: #F2F2F5;
      i {
        display: inline-block;
        width: 40px;
        height: 3px;
        background: #ddd;
        border-radius: 5px
      }
    }
    .nav-bar{
      position: relative;
      float: right;
      button, i, li, a, ul{
        transition: all 1s;        
      }
    }
    li{
      display: inline-block;
      width: 80px;
      height: 50px;
      text-align: center;
      a {
        display: block;
        height: 50px;
        color: #333;
      }
      &:hover {
        background: orange
      }
      &:hover a{
        color: #eee;
      }
    }
    .wei {
      display: none;
      width: 0;
      height: 0;
      border: 25px solid #999;
      border-bottom: 25px solid transparent
    }
  }
}
/* 窗口宽度<960,设计宽度=768 */
@media screen and (max-width:959px){
  #app {
    width: 768px;
    .main-nav {
      input {
        width: 270px
      }
    }
  }
}
/* 窗口宽度<768,设计宽度=640 */
@media screen and (max-width:767px){
  #app {
    width: 640px;
    .main-nav {
      input {
        width: 250px
      }
    }
    .nav-bar {
      &:hover ul {
        transform: scaleY(1)
      }
      &:hover li {
        transform: rotateX(0)
      }
      &:hover button{
        border: none;
        border-radius: 0;
        background: rgba(0,0,0,0.5);
      }
      &:hover i:nth-child(1){
        transform: scaleX(sqrt(2)) rotate(45deg)
      }
      &:hover i:nth-child(2){
        opacity: 0;
      }
      &:hover i:nth-child(3){
        transform: scaleX(sqrt(2)) rotate(-45deg)
      }
      ul {
        position: absolute;
        top: 50px;
        transform-origin: top;
        transform: scaleY(0);
        perspective: 200px
      }
      li{
        width: 46px;
        border-left: 2px dashed orange;
        border-right: 2px dashed orange;
        background: rgba(25, 25, 25, .2);
        transform-style: preserve-3d;
        transform-origin: top;
        transform: rotateX(90deg)
      }
      .wei{
        display: block
      }
      button {
        display: inline-block;
        i {
          transform-origin: left;
        }
      }
    }
  }
}
/* 窗口宽度<640,设计宽度=480 */
@media screen and (max-width:639px){
  #app {
    width: 480px;
    .main-nav {
      .nav-bar{
        width: 50px;
        border-radius: 5px;
      }
    }
  }
}
/* 窗口宽度<480,设计宽度=320 */
@media screen and (max-width:479px){
  #app {
    width: 320px;
    .main-nav {
      input {
        width: 150px
      }
    }
  }
}
</style>



